import { Component, OnInit } from '@angular/core';
import { GaKlaimPengaturanService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Klaim_pengaturan } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit-pengaturan',
  templateUrl: './edit-pengaturan.component.html',
  styleUrls: ['./edit-pengaturan.component.scss']
})
export class EditPengaturanComponent implements OnInit {

    constructor(
        private gaKlaimPengaturanService: GaKlaimPengaturanService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) { }

    data: Klaim_pengaturan = {}

    id_klaim_pengaturan: string = ''

    ngOnInit() {
        this.id_klaim_pengaturan = this.activatedRoute.snapshot.paramMap.get('id_klaim_pengaturan')
        this.form()
        this.getData()
    }

    getData() {
        this.spinner.show()
        this.gaKlaimPengaturanService.getGaKlaimPengaturansId(this.id_klaim_pengaturan).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
                this.spinner.hide()
            }
        )
    }

    dataForm: FormGroupTypeSafe<Klaim_pengaturan>
    form() {
        this.dataForm = this.fb.group<Klaim_pengaturan>({
            nama_plafon: new FormControl('', [Validators.required]),
            plafon_klaim: new FormControl('', [Validators.required]),
            id_klaim_kategori: new FormControl(''),
            id_user: new FormControl(''),
            id_klaim_pengaturan: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),

        })
    }


    getValue(key) {
        return this.dataForm.get(key).value
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.gaKlaimPengaturanService.putGaKlaimPengaturansId({id: this.id_klaim_pengaturan, body: this.data}).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/ga/request/module/pengaturan/klaim/list'])
    }

}
