import { Component, OnInit } from '@angular/core';
import { GaKlaimKategoriService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Klaim_kategori } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit-kategori',
  templateUrl: './edit-kategori.component.html',
  styleUrls: ['./edit-kategori.component.scss']
})
export class EditKategoriComponent implements OnInit {

    constructor(
        private gaKlaimKategoriService: GaKlaimKategoriService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) {}

    data: Klaim_kategori
    id_klaim_kategori: string = ''

    ngOnInit() {
        this.id_klaim_kategori = this.activatedRoute.snapshot.paramMap.get('id_klaim_kategori')
        this.form()
        this.getData()
    }

    getData() {
        this.gaKlaimKategoriService.getGaKlaimKategorisId(this.id_klaim_kategori).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
            }
        )
    }

    dataForm: FormGroupTypeSafe<Klaim_kategori>
    form() {
        this.dataForm = this.fb.group<Klaim_kategori>({
            nama_kategori: new FormControl('', [Validators.required]),
            jumlah_keluarga_ditanggung: new FormControl(''),
            tanggal_reload_klaim: new FormControl(''),
            id_klaim_kategori: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
        })
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.gaKlaimKategoriService.putGaKlaimKategorisId({id: this.id_klaim_kategori, body: this.data}).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/ga/request/module/pengaturan/klaim/list'])
    }

}
