import { GaKlaimPengaturanService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';
import { Klaim_pengaturan } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-add-pengaturan',
    templateUrl: './add-pengaturan.component.html',
    styleUrls: ['./add-pengaturan.component.scss']
})
export class AddPengaturanComponent implements OnInit {

    constructor(
        private gaKlaimPengaturanService: GaKlaimPengaturanService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) { }

    data: Klaim_pengaturan = {}

    id_klaim_kategori: string = ''

    ngOnInit() {
        this.id_klaim_kategori = this.activatedRoute.snapshot.paramMap.get('id_klaim_kategori')
        this.form()
    }

    dataForm: FormGroupTypeSafe<Klaim_pengaturan>
    form() {
        this.dataForm = this.fb.group<Klaim_pengaturan>({
            nama_plafon: new FormControl('', [Validators.required]),
            plafon_klaim: new FormControl('', [Validators.required]),
            id_klaim_kategori: new FormControl(this.id_klaim_kategori),

        })
    }


    getValue(key) {
        return this.dataForm.get(key).value
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.gaKlaimPengaturanService.postGaKlaimPengaturans(this.data).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/ga/request/module/pengaturan/klaim/list'])
    }


}
