import { EditKategoriComponent } from './edit-kategori/edit-kategori.component';
import { AddKategoriComponent } from './add-kategori/add-kategori.component';
import { EditPengaturanComponent } from './edit-pengaturan/edit-pengaturan.component';
import { AddPengaturanComponent } from './add-pengaturan/add-pengaturan.component';
import { ListComponent } from './list/list.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
    {
        path: '',
        redirectTo: 'list'
    },
    {
        path: 'list',
        component: ListComponent
    },
    {
        path: 'add-pengaturan/:id_klaim_kategori',
        component: AddPengaturanComponent
    },
    {
        path: 'edit-pengaturan/:id_klaim_pengaturan',
        component: EditPengaturanComponent
    },
    {
        path: 'add-kategori',
        component: AddKategoriComponent
    },
    {
        path: 'edit-kategori/:id_klaim_kategori',
        component: EditKategoriComponent
    }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class KlaimRoutingModule { }
