import { Component, OnInit } from '@angular/core';
import { GaKlaimKategoriService, GaKlaimPengaturanService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzMessageService } from 'ng-zorro-antd';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private gaKlaimKategoriService: GaKlaimKategoriService,
        private gaKlaimPengaturanService: GaKlaimPengaturanService,
        private spinner: NgxSpinnerService,
        private nzMessageService: NzMessageService,
    ) {}

    listClaimCategory: any[] = []
    ngOnInit() {
        this.getCutiKategori()
    }

    //   panggie kategori
    getCutiKategori() {
        this.gaKlaimKategoriService.getGaKlaimKategoris().subscribe(res => {
            this.listClaimCategory = res.data

            this.listClaimCategory.forEach((item, index) => {
                this.gaKlaimPengaturanService
                    .getGaKlaimPengaturans(
                        JSON.stringify({ id_klaim_kategori: item.id_klaim_kategori }),
                    )
                    .subscribe(dataRes => {
                        this.listClaimCategory[index]['claimPengaturan'] = dataRes.data
                    })
            })
        })
    }

    delete(id) {
        this.spinner.show()
        this.gaKlaimPengaturanService.deleteGaKlaimPengaturansId(id).subscribe(data => {
            this.spinner.hide()
        })
    }

    hapus(id) {
        this.spinner.show()
        this.gaKlaimKategoriService.deleteGaKlaimKategorisId(id).subscribe(data => {
            this.spinner.hide()
        })
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }


}
